import React from 'react'
import { Link } from 'react-router-dom'
import { ErrorResponse } from 'types'
import { TrainingModule } from 'modules/TrainingLibrary/types'
import trainingLibraryRoutes from 'modules/TrainingLibrary/routing/routes'
import { Wrapper } from './TrainingModules.styled'

type TrainingModulesProps = {
  loading?: boolean
  error?: ErrorResponse
  categoryName?: string
  modules?: TrainingModule[]
}

const TrainingModules: React.FC<TrainingModulesProps> = props => {
  const { loading, error, modules, categoryName } = props

  if (loading) return <div>loading...</div>
  if (error) return <div>{error.error}</div>

  const modulesCount = modules?.length
  if (!modulesCount) return <div>No trainigs available</div>

  return (
    <Wrapper>
      <div>{categoryName}</div>
      <div>Filter courses ...</div>
      <div>{modulesCount} trainigs available</div>
      <br />
      {modules?.map(
        ({ id, moduleName, discName, numberOfChapters, discRelease }) => (
          <Link to={`${trainingLibraryRoutes.course}/${id}`} key={id}>
            <div>
              Module: {moduleName} - {discName} ({numberOfChapters}/
              {discRelease})
            </div>
          </Link>
        )
      )}
    </Wrapper>
  )
}

export default React.memo(TrainingModules)
