import React from 'react'
import { Link } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { ErrorResponse } from 'types'
import { TrainingCategory } from 'modules/TrainingLibrary/types'
import trainingLibraryRoutes from 'modules/TrainingLibrary/routing/routes'
import { Wrapper } from './TrainingCategories.styled'

type TrainingCategoriesProps = {
  loading?: boolean
  error?: ErrorResponse
  categories?: TrainingCategory[]
}

const TrainingCategories: React.FC<TrainingCategoriesProps> = props => {
  const { t } = useTranslation('translations:trainingLibrary')
  const { loading, error, categories } = props
  if (loading) return <div>loading...</div>
  if (error) return <div>{error.error}</div>
  if (!categories) return <div>no categories</div>

  return (
    <Wrapper>
      <div>Filter - Training, category or course...</div>
      <div>{t('translations:trainingLibrary.allTrainingSubjects')}</div>
      <br />
      {categories.map(({ id, name, moduleIds }) => (
        <Link to={`${trainingLibraryRoutes.modules}/${id}`} key={id}>
          <div>
            Cat: {name} ({moduleIds?.length})
          </div>
        </Link>
      ))}
    </Wrapper>
  )
}

export default React.memo(TrainingCategories)
