import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import { RootState } from 'store'
import {
  getTrainingLibraryData,
  getModulesForCategory,
} from 'modules/TrainingLibrary/slices/trainingLibrary'
import TrainingModules from 'modules/TrainingLibrary/components/TrainingModules'

const TrainingModulesContainer: React.FC = () => {
  const { categoryId } = useParams()
  const dispatch = useDispatch()
  const { isLoading, error, categories, modules, categoryName } = useSelector(
    ({ trainingLibrary }: RootState) => {
      const { error, isLoading, result } = trainingLibrary.trainingLibraryData
      return {
        error,
        isLoading,
        categories: result?.categoriesById,
        // need "?" after categoriesById[categoryId] because categoryId any data from user
        categoryName: result?.categoriesById[categoryId]?.name,
        modules: getModulesForCategory(result, categoryId),
      }
    }
  )

  useEffect(() => {
    if (!categories) {
      dispatch(getTrainingLibraryData())
    }
  }, [dispatch, categories])

  return (
    <TrainingModules
      loading={isLoading}
      error={error}
      categoryName={categoryName}
      modules={modules}
    />
  )
}

export default TrainingModulesContainer
