import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from 'store'
import {
  getTrainingLibraryData,
  getCategories,
} from 'modules/TrainingLibrary/slices/trainingLibrary'
import {
  TrainingCategories,
  TrainingTypes,
} from 'modules/TrainingLibrary/components'

const TrainingLibraryContainer: React.FC = () => {
  const dispatch = useDispatch()
  const { isLoading, error, categories } = useSelector(
    ({ trainingLibrary }: RootState) => {
      const { error, isLoading, result } = trainingLibrary.trainingLibraryData
      return {
        error,
        isLoading,
        categories: getCategories(result),
      }
    }
  )

  useEffect(() => {
    if (!categories) {
      dispatch(getTrainingLibraryData())
    }
  }, [dispatch, categories])

  return (
    <>
      <TrainingCategories
        loading={isLoading}
        error={error}
        categories={categories}
      />
      <br />
      <TrainingTypes />
    </>
  )
}

export default TrainingLibraryContainer
