import React from 'react'
import { Link } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { SortUtils } from 'utils'
import trainingLibraryRoutes from 'modules/TrainingLibrary/routing/routes'
import { Wrapper } from './TrainingTypes.styled'

const TrainingTypes: React.FC = () => {
  const { t } = useTranslation('translations:login')
  const types = [
    {
      id: 1,
      name: t('translations:trainingLibrary.elearning'),
      count: 153,
    },
    {
      id: 2,
      name: t('translations:trainingLibrary.electronicDistanceCourse'),
      count: 24,
    },
    {
      id: 3,
      name: t('translations:trainingLibrary.rapidElearning'),
      count: 12,
    },
    {
      id: 4,
      name: t('translations:trainingLibrary.pocketGuides'),
      count: 8,
    },
    {
      id: 5,
      name: t('translations:trainingLibrary.learningFilms'),
      count: 19,
    },
    {
      id: 6,
      name: t('translations:trainingLibrary.surveys'),
      count: 25,
    },
  ]
  const sortedTypes = types.sort(SortUtils.sortAlphabet('name'))

  return (
    <Wrapper>
      <div>{t('translations:trainingLibrary.trainingTypes')}</div>
      <br />
      {sortedTypes.map(({ id, name, count }) => (
        <Link to={`${trainingLibraryRoutes.modules}/${id}`} key={id}>
          <div>
            Type: {name} ({count})
          </div>
        </Link>
      ))}
    </Wrapper>
  )
}

export default React.memo(TrainingTypes)
